'use client';

import { useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import { doc, setDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import Footer from '@/app/components/Footer';

export default function CompletarPerfilPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [formData, setFormData] = useState({
    dni: '',
    telefono: '',
    direccion: '',
  });
  const [error, setError] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    if (!user) {
      setError('No estás autenticado. Por favor, inicia sesión de nuevo.');
      return;
    }

    try {
      // Extraer nombre y apellido de displayName
      const nameParts = user.displayName?.split(' ') || [];
      const nombre = nameParts[0] || '';
      const apellido = nameParts.slice(1).join(' ') || '';

      const userProfile = {
        uid: user.uid,
        nombre,
        apellido,
        email: user.email,
        dni: formData.dni,
        telefono: formData.telefono,
        direccion: formData.direccion,
      };

      await setDoc(doc(db, 'clientes', user.uid), userProfile, { merge: true });
      
      alert('¡Perfil completado con éxito!');
      router.push('/');
    } catch (err) {
      console.error("Error al guardar el perfil:", err);
      setError('Hubo un error al guardar tu perfil. Por favor, inténtalo de nuevo.');
    }
  };

  return (
    <main className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold text-center mb-8">Completa tu Perfil</h1>
      <p className="text-center mb-8">Necesitamos algunos datos más para completar tu registro.</p>
      <form onSubmit={handleSubmit} className="max-w-md mx-auto bg-white p-8 rounded-lg shadow-md">
        {error && <p className="text-red-500 text-center mb-4">{error}</p>}
        <div className="mb-4">
          <label htmlFor="dni" className="block text-gray-700 font-bold mb-2">DNI</label>
          <input
            type="text"
            id="dni"
            name="dni"
            value={formData.dni}
            onChange={handleChange}
            className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-violet-500"
            required
          />
        </div>
        <div className="mb-4">
          <label htmlFor="telefono" className="block text-gray-700 font-bold mb-2">Teléfono</label>
          <input
            type="tel"
            id="telefono"
            name="telefono"
            value={formData.telefono}
            onChange={handleChange}
            className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-violet-500"
            required
          />
        </div>
        <div className="mb-6">
          <label htmlFor="direccion" className="block text-gray-700 font-bold mb-2">Dirección</label>
          <input
            type="text"
            id="direccion"
            name="direccion"
            value={formData.direccion}
            onChange={handleChange}
            className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-violet-500"
            required
          />
        </div>
        <button
          type="submit"
          className="w-full bg-violet-500 hover:bg-violet-700 text-white font-bold py-2 px-4 rounded-lg"
        >
          Guardar Perfil
        </button>
      </form>
      <Footer />
    </main>
  );
}
